<template>
    <div class="y-box">
        <input ref="input"  v-if="isClose" class="input"  type="text" @blur="close">
        <div class="y-box-own" @click="showItemF" @mousedown="down"  ref="switch">
            <slot class="button" name="button" ></slot>
        </div>
        <ul ref='ul' class="y-box-item"  :class="showItem?'open':'close'" @mousedown="ulmousedown" @mouseup="ulmouseup">
            <li class="item" v-for="item in columns" :key="item.key">
                <Expand :name='item.name' :render='item.render'></Expand>
            </li>
        </ul>
    </div>
</template>
<script>
import Expand from "./expand.js";
export default {
  name: "yCircleMenu",
  components: {
    Expand
  },
  data() {
    return {
      showHead: true,
      showItem: false,
      // itemÊúÄÂ§ßÂÆΩÂ∫¶
      itemMaxWidth: 0,
      // itemÊúÄÂ§ßÈ´òÂ∫¶
      itemMaxHeight: 0,
      // ÈªòËÆ§ÂúÜÁöÑÂçäÂæÑÈôÑÂä†ÂÄºÔºåÂú®Ëá™Âä®ÁîüÊàêÁöÑÂúÜÁöÑÂçäÂæÑÂü∫Á°Ä‰∏äÂä†‰∏äËøô‰∏™ÂÄº
      defaultOr: 50,
      // ÂúÜÂçäÂæÑ
      or: 0,
      // Á¨¨‰∫åË±°ÈôêxyËΩ¥È´òÂ∫¶
      oYX: {},
      // ÊØè‰∏™ÁÇπÁöÑ‰ΩçÁΩÆÊï∞ÁªÑ
      itemPoint: [],
      spacing: 20,
      // ÊØè‰∏™itemÂÆΩÈ´ò
      itemWH: [],
      effective: true,
      isClick: false
    };
  },
  props: {
    columns: {
      type: Array,
      default: function() {
        return [];
      }
    },
    direc: {
      type: String,
      default: "top"
    },
    // ÂúÜÂçäÂæÑÈôÑÂä†ÂÄº
    circleOr: {
      type: [Number, String],
      default: 0
    },
    // itemÂéüÁÇπ‰ΩçÁΩÆ
    itemO: {
      type: [String],
      default: "o"
    },
    // ÊòØÂê¶‰∏∫Êï¥ÂúÜ
    completeCircle: {
      type: [Number, String],
      default: 0.5
    },
    // ÊØè‰∏™itemÂä®ÁîªÊâßË°åÁöÑÊó∂Èó¥,Âçï‰ΩçÁßí
    duration: {
      type: [Number],
      default: 0.5
    },
    // ÊØè‰∏™itemÁöÑÂª∂ËøüÊó∂Èó¥,Âçï‰ΩçÁßí
    delay: {
      type: Number,
      default: 0.01
    },
    // ÁÇπÂáªÁ©∫ÁôΩÂå∫ÂüüÂÖ≥Èó≠
    isClose: {
      type: [Boolean, String],
      default: false
    },
    // ÁîüÊàêÂúÜÁöÑÂÅèÁßªÈáè
    offset: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  computed: {
    allAtems() {
      return this.$refs.ul.children;
    },
    // Ëé∑ÂèñÂçäÂúÜÂ§πËßí
    Angle() {
      let i = this.deg === 360 ? 0 : 1;
      return this.deg / (this.allAtems.length + i);
    },
    deg() {
      return 360 * this.completeCircle;
    },
    // Âú®‰ª•ÂºÄÂÖ≥‰∏∫ÂùêÊ†áËΩ¥ÁöÑÁ¨¨‰∫åË±°ÈôêÂÖÉÁ¥†Êï∞Èáè
    // Â¶ÇÊûúÊúâ.5ÁöÑÊÉÖÂÜµÂàô‰∏∫‰∏≠Èó¥ÂÖÉÁ¥†ËêΩÂà∞xËΩ¥‰∏ä
    twoQuadrant() {
      return this.allAtems.length / 2;
    },
    switchDom() {
      return this.$refs.switch;
    },
    switchWH() {
      let w = this.switchDom.getBoundingClientRect().width;
      let h = this.switchDom.getBoundingClientRect().height;
      return {
        width: w,
        height: h
      };
    }
  },
  beforeUpdate() {
    this.init();
  },
  mounted() {
    let a = this.itemsWH;
    if (this.$slots.header === undefined) {
      this.showHead = false;
    } else {
      this.showHead = true;
    }
    this.init();
  },
  methods: {
    init() {
      this.getY();
      this.getOr();
      this.setItemXY();
    },
    ulmouseup() {
      this.effective = true;
    },
    // ÁÇπÂáªli‰∏çÊî∂Ëµ∑ÂúÜ
    ulmousedown(e) {
      if (!this.isClose) {
        return;
      }
      if (e.target) {
        this.effective = false;
      } else {
        this.effective = true;
      }
      this.$refs.input.focus();
    },
    // Ëé∑ÂèñÊØè‰∏™Â≠êÂÖÉÁ¥†ÂÆΩÈ´ò
    itemsWH(i) {
      return {
        width: this.allAtems[i].getBoundingClientRect().width,
        height: this.allAtems[i].getBoundingClientRect().height
      };
    },
    // Ëé∑ÂæóxyËΩ¥È´òÂ∫¶
    getY() {
      let width = 0,
        height = 0;
      for (let i = 0, length = this.allAtems.length; i < length; i++) {
        const o = this.itemsWH(i);
        width += o.width;
        height += o.height;
        this.itemWH.push({
          w: o.width,
          h: o.height
        });
      }
      // Â¶ÇÊûúÊòØÊ≠£ÂúÜ,Âàô‰∏Ä‰∫å„ÄÅ‰∏âÂõõË±°ÈôêitemÁöÑÊï∞Èáè‰∏∫ÊÄªÊï∞ÈáèÁöÑ‰∏ÄÂçäÔºåÊâÄ‰ª•ÂçäÂæÑ‰∏∫ÂçäÂúÜÁöÑ‰∏ÄÂçä
      let i = this.deg == 360 ? 2 : 1;
      let oYX = {
        x: width / i,
        y: height / i
      };
      this.oYX = oYX;
    },
    // Ëé∑ÂæóÂúÜÂçäÂæÑ,Ê†πÊçÆitem Êï∞ÈáèÁöÑ‰∏ÄÂçäËé∑ÂèñÔºåxÂÄºÂøÖÈ°ªÊ†πÊçÆÂçäÂæÑËé∑Âæó
    getOr() {
      let { x, y } = this.oYX;
      x = x / 2;
      y = y / 2;
      this.or = x + Number(this.circleOr);
    },
    // ËÆæÂÆöÊâÄÊúâitem‰∏≠ÊúÄÂ§ßÂÆΩÂ∫¶ÂíåÊúÄÂ§ßÈ´òÂ∫¶
    setItemMax() {
      let ul = this.$refs.ul;
      let childrens = ul.childrens;
      let widthArr = [],
        heightArr = [];
      childrens.forEach((v, i) => {
        itemObj = this.itemsWH(i);
        widthArr.push(itemObj.width);
        heightArr.push(itemObj.height);
      });
      this.itemMaxWidth = Math.max(...widthArr);
      this.itemMaxHeight = Math.max(...widthArr);
    },
    close(event) {
      this.isClick = true;
      if (!this.effective) {
        this.effective = true;
        this.$refs.input.focus();
        return;
      }
      if (!this.showItem) return;
      this.showItem = false;
    },
    down() {
      this.isClick = false;
    },
    showItemF(event) {
      if (this.isClick) {
        this.isClick = false;
        return;
      }
      this.showItem = !this.showItem;
    },
    animateItem() {
      if (this.isClose) this.$refs.input.focus();
      [...this.allAtems].forEach((v, i) => {
        v.style.transform = `translate(${this.itemPoint[i].x}px,${
          this.itemPoint[i].y
        }px)`;
        v.style.transition = `all ${this.duration}s ${i * this.delay}s`;
      });
    },
    reset() {
      [...this.allAtems].forEach(v => {
        v.style.transform = `translate(0,0)`;
      });
    },
    // ËÆæÁΩÆÊØè‰∏Ä‰∏™ÂÖÉÁ¥†ÁöÑx,yÂÄº
    setItemXY() {
      let xd = this.setdirec().xd;
      let yd = this.setdirec().yd;
      let switchB = this.switchWH;
      let offsetX = this.offset.x || 0;
      let offsetY = this.offset.y || 0;
      // Â∞ÜÂúÜÂøÉÂÆö‰ΩçÂú®ÂºÄÂÖ≥‰∏≠ÂøÉ
      let ox = switchB.width / 2;
      let oy = switchB.height / 2;
      this.itemPoint = [...this.allAtems].map((v, i) => {
        let x =
          xd * this.or * Math.sin((this.Angle * (i + 1) * 2 * Math.PI) / 360);
        let y =
          yd * this.or * Math.cos((this.Angle * (i + 1) * 2 * Math.PI) / 360);
        if (this.direc == "top" || this.direc == "bottom") {
          // ‰∏ä‰∏ãÂàÜÂ∏É
          [x, y] = [y, x];
        }
        x = x + ox + offsetX;
        y = y + oy + offsetY;
        const h = this.itemsWH(i).height;
        const w = this.itemsWH(i).width;
        return {
          y: this.calculateItemO(y, h, "y"),
          x: this.calculateItemO(x, w, "x")
        };
      });
    },
    // ÊâÄÊúâitemÊéíÂ∏ÉÊñπÂºè
    setdirec() {
      switch (this.direc) {
        case "left":
          return {
            xd: -1,
            yd: 1
          };
          break;
        case "right":
          return {
            xd: 1,
            yd: -1
          };
          break;
        case "bottom":
          return {
            xd: 1,
            yd: 1
          };
          break;
        case "top":
          return {
            xd: -1,
            yd: -1
          };
          break;
        default:
          console.error("direcÂèÇÊï∞ÊúâËØØ");
          break;
      }
    },
    // itemÁöÑÂéüÁÇπ‰ΩçÁΩÆ
    // type:x/y
    calculateItemO(itemXorY, wh, type) {
      const o = itemXorY > 0 ? 1 : -1;
      itemXorY = Math.abs(itemXorY);
      if (type == "x") {
        switch (this.itemO) {
          case "top":
            return o * itemXorY;
            break;
          case "right":
            if (o < 0) {
              return o * (itemXorY + wh);
            } else {
              return o * (itemXorY - wh);
            }
            break;
          case "bottom":
            if (o < 0) {
              // ‰∫å‰∏âÊÉ≥Ë±°
              return o * (itemXorY + wh);
            } else {
              // ‰∏ÄÂõõË±°Èôê
              return o * (itemXorY - wh);
            }
            break;
          case "left":
            return o * itemXorY;
            break;
          case "o":
            if (o < 0) {
              // ‰∫å‰∏âË±°Èôê
              return o * (itemXorY + wh / 2);
            } else {
              // ‰∏ÄÂõõË±°Èôê
              return o * (itemXorY - wh / 2);
            }
            break;
          default:
            return o;
            break;
        }
      } else {
        // YËΩ¥
        switch (this.itemO) {
          case "top":
            return o * itemXorY;
            break;
          case "right":
            return o * itemXorY;
            break;
          case "bottom":
            // ‰∏âÂõõË±°Èôê
            if (o > 0) {
              return o * (itemXorY - wh);
            } else {
              // ‰∏Ä‰∫åË±°Èôê
              return o * (itemXorY + wh);
            }
            break;
          case "left":
            if (o < 0) {
              return o * (itemXorY + wh);
            } else {
              return o * (itemXorY - wh);
            }
            break;
          case "o":
            if (o > 0) {
              return o * (itemXorY - wh / 2);
            } else {
              // ‰∏Ä‰∫åË±°Èôê
              return o * (itemXorY + wh / 2);
            }
            break;
          default:
            return o;
            break;
        }
      }
    }
  },
  watch: {
    showItem: function(newD, oldD) {
      this.showItem ? this.animateItem() : this.reset();
    }
  }
};
</script>

<style scoped>
ul.y-box-item {
  list-style: none;
}
.y-box-item {
  position: absolute;
  top: 0;
  left: 0;
}
.y-box-item {
  z-index: -1;
}
.y-box {
  position: relative;
  display: inline-block;
  z-index: 66;
}
.y-box > .mask {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.y-box > .input {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
  overflow: hidden;
}
.y-box-item > .item {
  position: absolute;
  transition: all 0.5s;
}
.close.y-box-item > .item {
  opacity: 0;
  overflow: hidden;
}
</style>





